import header2 from '@/components/header/header2';
import cardInfocomponent from '@/components/paychannel/cardInfocomponent';
import {mapState} from 'vuex';
export default {
    data() {
        return {

        };
    },
    computed: {
        ...mapState(['cardInfo'])
    },
    watch: {

    },
    methods: {
        rescission() {
            this.axios({
                method: 'post',
                url: '/Api/GuiPayByqiao',
                data: '{"id":19,"jsonrpc":"2.0","method":"cancelSign","params":{"SgnNo":"' + this.cardInfo.SgnNo + '"}}',
                contentType: 'application/json',
                dataType: 'json'
            }).then((result) => {
                if (result.data.result.code === 50000) {
                    this.$router.push({path: `/payChannel`});
                } else {
                    this.$vux.toast.text('解约失败', 'bottom');
                }
            });
        }
    },
    mounted() {
    },
    created() {

    },
    components: {
        header2,
        cardInfocomponent
    }
};



